/* ------------------------------  Start header ------------------------------ */
.header {
  position: absolute;
  top: -107px;
  width: 890px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}
.header ul.step {
  overflow: hidden;
}
.header ul.step li {
  overflow: hidden;
  position: relative;
  width: 38px;
  height: 38px;
  margin-right: 18px;
  line-height: 2.1em;
  text-align: center;
  border: 1px solid #f0f0f0;
  border-radius: 50%;
  color: #d8dad4;
  background: #fff;
  float: left;
}
.header ul.step li span {
  display: block;
  width: 38px;
  height: 38px;
  margin: 0px 0 0 -1px;
}
.header ul.step li span.current {
  color: #2d2d2d ;
}
.header ul.step li span.active {
  background: url(data:image/svg+xml;base64,) no-repeat center;
  background-size: 50%;
  /*margin: -2px 0 0 -2px;*/
  color: rgba(0,0,0,0);
}
.header ul.step li span.active-curent {
  -webkit-animation: stepanim 1s linear 0 alternate;
  animation: stepanim 1s linear 0s alternate;
}
.header a.refresh {
  display: inline-block;
  width: 22px;
  height: 27px;
  margin-top: -31px;
  float: right;
  text-decoration: none;
  background: url(data:image/svg+xml;base64,) no-repeat;
  background-size: contain;
}
.header a.refresh:hover {
  background: url(data:image/svg+xml;base64,) no-repeat;
  background-size: contain;
}
.header .refresh-background {
  display: block;
  float: right;
  width: 46px;
  height: 46px;
  margin: -40px 0 0 0;
  text-align: center;
  line-height: 3.4;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
}
.header .refresh-background a.refresh {
  display: inline-block;
  width: 22px;
  height: 27px;
  margin-top: 0;
  float: none;
}
.header .description {
  margin: 13px 0 43px -4px;
