.step-animation{
  animation: stepanim 1s linear .75s alternate;
    -webkit-animation: stepanim .75s linear 1 alternate;
}

.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}


































































































































































































































































































































































































